/*
=========================================
НА ТАБЛЕТАХ И МОБИЛЬНЫХ
КЛОНИРУЕТ ВСЕ ".MAIN-NAV" В МОД.ОКНО
========================================= */
// JS не скрывает осн.меню!
// Управляет сокрытием CSS блока main-nav.

const MODIFICATOR = 'main-nav--modal';
const modal = document.querySelector("#modal--main-nav");
const modalBody = modal.querySelector(".modal-body");
let isPlacedToModal = false;

const menus = (function(elements) {
  if (elements === null) { return null; }
  const result = [];
  elements.forEach(function(el) {
    result.push({
      element: el,
      parent: el.parentElement,
      nextSibling: el.nextElementSibling || null,
    });
  });
  return result;
})(document.querySelectorAll(".main-nav"));


