/*
=========================================
  ИНИЦИАЛИЗИРУЕТ МОД.ОКНО МОДИФИКАТОРА
========================================= */
export function processingModalContainsForm(modalModif) { // eslint-disable-line import/prefer-default-export
  $(function() {
    const modal = document.querySelector('#modal--' + modalModif);
    const form = modal.querySelector('.form--' + modalModif);
    const $requiredArr = $(modal).find('input').filter('[required]:visible');
    const modalDialog = modal.querySelector(".modal-dialog");

    // ИНИЦИАЛИЗАЦИЯ
    $(modal).on('shown.bs.modal', function() {
      // Фокус на первом поле ввода
      // $(this).find('input:not([type=hidden])').eq(0).focus();

      // Отправка формы и валидация
      form.addEventListener("submit", function(evt) {
        evt.preventDefault(); // предотвращает передачу php-скрипту

        // Проверка обязательных значений на пустоту
        $requiredArr.each(function(index, val) {
          if (!val.value) {
            // Анимация: тряска
            modalDialog.classList.add("modal--shake");
            setTimeout(function() { modalDialog.classList.remove("modal--shake"); }, 600);
            return false;
          }
        });
      });
    });
  });
}


/*
=========================================
  BODY SCROLL LOCK
========================================= */
const bodyScrollLock = require('body-scroll-lock');

const {disableBodyScroll} = bodyScrollLock;
const {enableBodyScroll} = bodyScrollLock;
const $modals = $('.modal');
let enabledBodyScrollLock = false;

$(function() {
  function initBodyScrollLock() {
    if (lib.isTabletDown === false || enabledBodyScrollLock) { return; }

    // Отключить body-скролл
    $($modals).on('shown.bs.modal', function() {
      disableBodyScroll(this);
    });
    // Включить body-скролл
    $($modals).on('hidden.bs.modal', function() {
      enableBodyScroll(this);
    });

    enabledBodyScrollLock = true;
    $(window).off('resize.bodyScrollLock');
  }

  // Показ бургера только на таблетах и мобильных
  initBodyScrollLock();
  // При ресайзе — только включает
  $(window).on('resize.bodyScrollLock', lib.throttle(function() { initBodyScrollLock(); }, 300));
});


/* ЭФФЕКТЫ
========================================= */
$(function() {
  /* АНИМАЦИЯ ПРИ ЗАКРЫТИИ МОД.ОКНА */
  /**
   * Проверяет наличие атрибутов:
   * data-animate-close="<css-класс анимации закрытия>" [data-animate-close-duration="<300>"]
   */
  $('.modal[data-animate-close]').on('hide.bs.modal', function(evt) {
    const closeAnimation = this.getAttribute("data-animate-close");
    // Очистка (закроет окно штатным обработчиком)
    if (this.classList.contains(closeAnimation)) {
      this.classList.remove(closeAnimation);
      return;
    }
    // Проиграть анимацию закрытия
    evt.preventDefault();
    const closeAnimateDuration = parseInt((this.getAttribute("data-animate-close-duration") || 200), 10);
    const $currModal = $(this);
    $currModal.addClass(closeAnimation);
    setTimeout(function() {
      $currModal.modal('hide');
    }, closeAnimateDuration);
  });


  /* ОЧИСТКА: ЗАКРЫТЬ ОТ МОД.ОКНА */
  $('.modal').on('show.bs.modal', function(evt) {
    const $prevOpenedModal = $('.modal.in');
    if ($prevOpenedModal.length > 0) {
      evt.preventDefault();
      $prevOpenedModal.modal('hide');
      $(this).modal('show');
    }
  });
});
