/**
 * БУРГЕР: ПОКАЗ/СОКРЫТИЕ И ФИКСАЦИЯ
 * Ограничения:
 *  — MAX 1 эл-т с классами .<opt.burgerClass>.<opt.burgerFixedClass> (иначе, возьмет только первый)
 */

const opt = {
  burgerClass: 'burger--modal', // [burger||burger--modal]
  burgerFixedClass: 'burger--fixed',
  burgerFixedJsClass: 'burger--fixed-js',
  fixedAncestorClass: 'fixed', // Для исключения: если предок фиксирован, то бургер фиксировать не надо
  breakpointToShow: () => lib.isMobile, // [lib.isTabletDown||lib.isMobile]
  breakpointToFix: () => lib.isTabletDown, // ТРЕБОВАНИЕ: breakpointToFix<=breakpointToShow
  displayVal: 'flex', // [flex||block] — flex для --icon-css
};


$(function() {
  const burger = document.querySelector('.'+opt.burgerClass);
  if (burger === null) { return; }


  /* ПОКАЗ БУРГЕРА (обычно только на таблетах и мобильных)
  ========================================= */
  function showBurger() {
    burger.style.display = opt.displayVal;
  }


  if (opt.breakpointToShow() === true) {
    showBurger();
  }


  /* ФИКСАЦИЯ
  ========================================= */
  const getFixedVisible = () => burger.classList.contains(opt.burgerFixedJsClass);

  // Статус в рамках текущего viewport'а
  function getBurgerFixedVpStatus() {
    const fixedAncestor = $(burger).closest('.'+opt.fixedAncestorClass).get(0);

    if (burger.classList.contains(opt.burgerFixedClass) !== true
      || !lib.isVerticalScrollPresent()
    ) {
      return false;
    } else if (
      !opt.breakpointToFix()
      || (fixedAncestor !== undefined && getComputedStyle(fixedAncestor).position === 'fixed')
    ) {
      return false;
    }
    return true;
  }

  function setBurgerFixed() {
    // Убрать фиксацию
    if (getFixedVisible() && lib.isFirstScreen(0.33)) {
      $(burger).removeClass(opt.burgerFixedJsClass);
    // Зафиксировать
    } else if (!getFixedVisible() && !lib.isFirstScreen(0.33)) {
      $(burger).addClass(opt.burgerFixedJsClass);
    }
  }

  function onScrollFixBurger() {
    if (getBurgerFixedVpStatus()) {
      // Set position:fixed
      setBurgerFixed();
      let timeoutID;
      $(window).on('scroll.burgerfix', function() {
        clearTimeout(timeoutID);
        timeoutID = setTimeout(setBurgerFixed, 300);
      });
    }
  }


  // FLOW
  onScrollFixBurger();


  /* RESIZE
  ========================================= */
  // Resize
  $(window).on('resize', lib.throttle(() => {
    // Очистка
    burger.style = "";
    burger.classList.remove(opt.burgerFixedJsClass);
    $(window).off('scroll.burgerfix');

    // hide
    if (opt.breakpointToShow() !== true) {
      return;
    }
    // show
    showBurger();
    // fix
    onScrollFixBurger();
  }, 300));
});
