/*
=========================================
  Создаём пространство имён Lib (es5)
=========================================
  Предоставляет глобальные "публичные" методы */

import PRESET from './preset';

const lib = (function() {
  /* Проверка полной ширины окна (включая scroll)
  ========================================= */
  // Соответствует CSS-вьюпорту
  const isWindowWidthSmaller = function(size) {
    const w1 = window.innerWidth;
    const w2 = $(window).width();
    const windowWidth = (w1 && w2) ? Math.max(w1, w2) : w1;
    return (windowWidth < size);
  };


  /* Проверка на вхождение в брейкпоинт
  ========================================= */
  /**
   * Вычисляет является ли текщий вьюпорт больше заданного.
   * Вешает на ресайз обработчик перевычисляющий значение.
   * @param  {[num]}  breakpoint [Предустановленное значение из preset-модуля]
   * @param  {[str]}  propName   [Название свойства модуля в кот.присвоится значение]
   * @return {Boolean}
   */
  function isViewportSmaller(breakpoint, propName) {
    const brkpt = breakpoint - 0.01; // 767.99

    // Обработчик на ресайз окна — чтоб хранил актуальное значение
    (function(brk, prp) {
      $(window).on('resize', function() {
        window.lib[prp] = isWindowWidthSmaller(brk);
      });
    })(brkpt, propName);

    return isWindowWidthSmaller(brkpt);
  }


  /* Работа с LocalStorage
  ========================================= */
  // Запись
  const saveToStorage = function(field, formID) {
    let name = $(field).attr('name');
    const value = $(field).val();
    if (formID) { name = formID +'__'+ name; } // префикс (formID_) для уникализации ключа
    if (name && (value.replace(/\s/g, "").length > 0)) {
      localStorage.setItem(name, value);
    }
  };
  // Чтение (@param: форма-<Х>)
  const loadFromStorage2Form = function(formElement) {
    const formID = formElement.id;
    $(formElement).find('input, textarea').not('[type="hidden"]').each(function(indx, element) { // input[type="text"]
      let name = $(element).attr('name');
      name = formID+'__'+name; // префикс для уникализации ключа
      const value = localStorage.getItem(name);
      if (value) {
        $(element).val(value);
      }
    });
  };


  /**
   * Throttle don't allow to our function to execute more than once every X milliseconds.
   *
   * throttle guarantees the execution of the function regularly,
   * at least every X milliseconds.
   * Like checking every 200ms your scroll position to trigger a CSS animation.
   */
  const throttle = function(fn, wait) {
    let time = Date.now();
    return function() {
      if ((time + wait - Date.now()) < 0) {
        fn();
        time = Date.now();
      }
    };
  };

  // Координаты эл-та относительно станицы (документа)
  const getDocCoords = function(el) {
    const box = el.getBoundingClientRect();
    return {
      top: box.top + window.pageYOffset,
      left: box.left + window.pageXOffset,
    };
  };


  /**
  * Vertical scrollbar is present?
  * Проверка: есть ли у страницы вертикальный скролл?
  * @return {Boolean}
  */
  function isVerticalScrollPresent() {
    return (document.documentElement.scrollHeight > document.documentElement.clientHeight);
  }

  function isFirstScreen(windowOffset = 1) { // [0—1] % окна прозапас
    return ($(window).scrollTop() + document.documentElement.clientHeight*windowOffset) <= document.documentElement.clientHeight;
  }

  /*
  ========================================= */
  /* eslint-disable object-shorthand */
  return {
    isMobile: isViewportSmaller(PRESET.BREAKPOINT_MD, 'isMobile'), // хранит вычесленный результат
    isTabletDown: isViewportSmaller(PRESET.BREAKPOINT_LG, 'isTabletDown'), // хранит вычесленный результат
    // isWindowWidthSmaller,
    saveToStorage,
    loadFromStorage2Form,
    throttle,
    getDocCoords,
    isVerticalScrollPresent,
    isFirstScreen,
  };
  /* eslint-enable */
})();

export default lib;
