import maskInput from "vanilla-text-mask/dist/vanillaTextMask";

// require("./form--validation.js");

$(function() {
  /*
  =========================================
    ПРЕДЗАПОЛНЕНИЕ ИЗ LOCALSTORAGE
  ========================================= */
  const allForms = document.querySelectorAll('form');

  if (allForms.length > 0) {
    for (let len = allForms.length, i = 0; i < len; i++) {
      const form = allForms[i]; // currForm

      // Достаем сорхраненное из локалстореджа
      window.lib.loadFromStorage2Form(form);

      // Запись данных введенного поля в localStorage
      $(form).find('input, textarea').not('[type="hidden"]').on('blur', function() {
        window.lib.saveToStorage(this, form.id);
      });
    }
  }


  /*
  =========================================
    МАСКА ДЛЯ ПОЛЯ ВВОДА ТЕЛЕФОНА
  ========================================= */
  // SET CURSOR POSITION
  $.fn.setCursorPosition = function(pos) {
    this.each(function(index, elem) {
      if (elem.setSelectionRange) {
        elem.setSelectionRange(pos, pos);
      } else if (elem.createTextRange) {
        const range = elem.createTextRange();
        range.collapse(true);
        range.moveEnd('character', pos);
        range.moveStart('character', pos);
        range.select();
      }
    });
    return this;
  };

  // +7 (999) 999-99-99
  const maskPattern = ['+', '7', ' ', '(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/];
  document.querySelectorAll('input[type=tel]').forEach(function(el) {
    maskInput({
      inputElement: el,
      mask: maskPattern,
      showMask: false,
      guide: true, // default: true
    });

    // Устанавливает курсор после части уже введенных символов
    el.addEventListener('focus', function(evt) {
      const val = evt.target.value;
      if (val.indexOf('_') !== -1) {
        const valPart = val.slice(0, val.indexOf('_'));
        evt.target.value = valPart;
      }
    });
  });
});
