const Parallax = require("parallax-js/dist/parallax.min.js");

$(function() {
  /*
  =========================================
    ДЕАКТИВИРУЕТ TEL-ССЫЛКИ НА ДЕСКТОПАХ
  ========================================= */
  const telLinks = document.querySelectorAll('a[href^="tel:"]');
  let isTelLinksDisabled = false;

  const disableTelLinks = function() {
    telLinks.forEach(function(el, idx) {
      if (!el.dataset.href) {
        el.dataset.href = el.getAttribute('href');
      }
      el.removeAttribute('href');
    });
    isTelLinksDisabled = true;
  };

  const enableTelLinks = function() {
    telLinks.forEach(function(el, idx) {
      el.setAttribute('href', el.dataset.href);
    });
    isTelLinksDisabled = false;
  };


  // FLOW
  if (!window.lib.isMobile) {
    disableTelLinks();
  }

  // Обработчик на ресайз окна
  $(window).on('resize', function() {
    if (isTelLinksDisabled === true && window.lib.isMobile === true) {
      // ВКЛ.
      enableTelLinks();
    } else if (isTelLinksDisabled === false && window.lib.isMobile === false) {
      // ОТКЛ.
      disableTelLinks();
    }
  });


  /*
  =========================================
    ПЛАВНАЯ ЯКОРНАЯ НАВИГАЦИЯ
  ========================================= */
  $("body").on('click', "a[href^='#']:not([data-toggle='modal'])", function(evt) {
    if (!this.hash || !$(this.hash).length) { return; }

    evt.preventDefault();
    const shiftTop = 50;
    $("html,body").animate({scrollTop: $(this.hash).offset().top - shiftTop}, 1000);

    // Если Моб.меню: Закрыть мод.окно (после клика в меню)
    const modalMainNav = $(this).parents('#modal--main-nav');
    if (modalMainNav.length > 0 && lib.isTabletDown === true) {
      $(modalMainNav).modal('hide');
    }
  });

  /*
  =========================================
    Parallax
  ========================================= */
  (() => {
    /* eslint-disable no-new */
    // header
    new Parallax(document.getElementById('scene-circles'));
    new Parallax(document.getElementById('scene-waves-top'));
    if (lib.isMobile === false) { new Parallax(document.getElementById('scene-waves-bottom')); }
    new Parallax(document.getElementById('scene-persons'));
    // 2nd screen
    if (lib.isMobile === false) { new Parallax(document.getElementById('scene-promo')); }
  })();
});
