/*
=========================================
  Slick Slider -> Конфигурация
========================================= */
$(function() {
  const slickInit = function(selector) {
    $(selector).slick({
      mobileFirst: true, // from low resolutions
      infinite: true,
      dots: true,
      arrows: true,
      prevArrow: '<button class="slider__arrow  slider__arrow--prev"></button>',
      nextArrow: '<button class="slider__arrow  slider__arrow--next"></button>',
      customPaging: function(slider, i) {
        return '<button class="slider__dot" type="button"></button>';
      },
      /*
      responsive: [
        {
          breakpoint: 830,
          settings: {
            arrows: false,
            // settings: "unslick",
          }
        },
      ],
      */
    });
  };

  // FLOW
  slickInit('.slider');
});
